import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import Texttipical from './texttipical.js';
import AnimationLandingpage from './animationLandingpage'

class Landing extends React.Component {
    render() {
        return (



            <Grid className="landing-grid">
                <Cell col={12}>

                    <AnimationLandingpage />

                    <div className="banner-text">

                        <Texttipical
                            text="Desenvolvedor Front-end"
                            size="h1"
                        />
                        <hr />
                        <p>HTML/CSS | JavaScript | NodeJS | React </p>

                        <div className="social-links">
                            <p>Clique para Acessar: </p>

                            {/*LinkedIn*/}
       
                            <a href="https://www.linkedin.com/in/arildo-magno-3a34a785" rel="noopener noreferrer" target="_blank" title="LINKEDIN">

                                <i className="fa fa-linkedin-square" aria-hidden="true">   <figcaption>LinkedIn </figcaption> </i>
                            </a>
                            

                            {/*GitHub*/}
                            <a href="https://github.com/ArildoMagno" rel="noopener noreferrer" target="_blank" title="GITHUB">
                                <i className="fa fa-github-square" aria-hidden="true"> <figcaption>GitHub </figcaption></i>

                            </a>
         
                        </div>
                      
                    </div>
                  <br/>
                </Cell>

            </Grid>

        );
    }
}

export default Landing;